.navBar {
  height: 80px;
  left: 0;
  width: 100%;
  z-index: 10;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 1px solid #ebebeb;
}

.navContainer {
  align-items: center;
  display: flex;
  flex-direction: row;
  width: 84.078%;
  justify-content: space-between;
}

.navLogo {
  height: 32px;
}

.userInteraction {
  width: fit-content;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.link {
  text-decoration: none;
  color: rgb(54, 54, 54);
  font-weight: 600;
  margin: 10px;
  cursor: pointer;
}
